.profile__text-name {
    font-size: 42px;
    line-height: 56px;
    font-weight: 500;
    margin: 0;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
}

@media screen and (max-width: 619px) {
    .profile__text-name {
        font-size: 27px;
        text-align: center;
    }
}